@charset "UTF-8";
.main {
  display: flex;
  flex-wrap: wrap;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hang {
  position: relative;
  width: 20em;
  height: 15em;
  margin: 0 auto;
}
@media (min-width: 1280px) {
  .hang {
    margin: 0;
    width: 30em;
    height: 25em;
  }
}

.gallows {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.gallows__goriz {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 17em;
  height: 0.4em;
  background-color: black;
}
@media (min-width: 1280px) {
  .gallows__goriz {
    width: 27em;
  }
}

.gallows__goriz-two {
  position: absolute;
  top: 1.5em;
  left: 40%;
  transform: translateX(-50%);
  width: 5.5em;
  height: 0.4em;
  background-color: black;
}
@media (min-width: 1280px) {
  .gallows__goriz-two {
    width: 9em;
  }
}

.gallows__vert {
  position: absolute;
  bottom: 0;
  left: 25%;
  width: 0.6em;
  height: 13.5em;
  background-color: black;
}
@media (min-width: 1280px) {
  .gallows__vert {
    height: 23.5em;
  }
}

.gallows__vert-two {
  position: absolute;
  top: 1.5em;
  left: 50%;
  width: 0.2em;
  height: 3.7em;
  background-color: black;
}
@media (min-width: 1280px) {
  .gallows__vert-two {
    height: 4.2em;
  }
}

.character {
  position: absolute;
  top: 0;
  left: 50%;
  display: flex;
  justify-content: center;
}

.character__head {
  position: absolute;
  top: 5em;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  border: 0.3rem solid black;
}
@media (min-width: 1280px) {
  .character__head {
    top: 5.5em;
    width: 2.1em;
    height: 2.1em;
  }
}

.character__body {
  position: absolute;
  top: 6.5em;
  width: 0.3em;
  height: 2.5em;
  background-color: black;
}
@media (min-width: 1280px) {
  .character__body {
    top: 7.5em;
    height: 4em;
  }
}

.character__hand {
  position: absolute;
  top: 6.8em;
  width: 0.3em;
  height: 1.7em;
  transform-origin: top;
  background-color: black;
}
@media (min-width: 1280px) {
  .character__hand {
    top: 7.8em;
    height: 2.7em;
  }
}

.hand--left {
  transform: rotate(30deg);
}

.hand--right {
  transform: rotate(-30deg);
}

.character__leg {
  position: absolute;
  top: 9em;
  width: 0.3em;
  height: 2em;
  transform-origin: top;
  background-color: black;
}
@media (min-width: 1280px) {
  .character__leg {
    top: 11em;
    height: 4em;
  }
}

.leg--left {
  transform: rotate(20deg);
}

.leg--right {
  transform: rotate(-20deg);
}

.question__container {
  width: 100%;
}
@media (min-width: 768px) {
  .question__container {
    margin: auto 0;
    max-width: 25rem;
  }
}

.question__list {
  display: flex;
  justify-content: center;
}

.question__el + .question__el {
  margin-left: 0.3em;
}

.question__el {
  width: 1.4em;
  border-bottom: 2px solid black;
  padding: 0;
  margin-top: 1.5em;
  height: 1.7em;
}

.question__letter {
  text-align: center;
  margin: 0;
  font-size: 1.3rem;
  animation: appear 1s ease-in;
  animation-fill-mode: both;
}

@keyframes appear {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 1;
  }
}
.question {
  display: flex;
  justify-content: center;
  text-align: center;
}

.question__text {
  margin-left: 0.3rem;
  font-weight: 700;
}

.errors {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: -0.7em;
}

.errors__text {
  margin: 0;
}

.errors__numbers {
  margin: 0 0 0 0.3rem;
  color: rgb(205, 9, 9);
}

.keyboard__list {
  display: flex;
  margin-top: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.keyboard__el {
  margin-top: 0.7rem;
}

.keyboard__el + .keyboard__el {
  margin-left: 0.3rem;
}

.keyboard__btn {
  display: block;
  width: 2em;
  height: 2em;
  border-radius: 20%;
  color: var(--block-text-color);
  background-color: var(--block-color);
}

.keyboard__btn:hover {
  opacity: 0.7;
}

.keyboard__btn:active {
  opacity: 0.2;
}

.keyboard__btn:disabled {
  opacity: 0.2;
  cursor: default;
}

.modal {
  position: fixed;
  margin: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1em;
  box-shadow: 0 0 10px black;
}

.modal div {
  display: flex;
}

.modal__title {
  text-align: center;
  font-size: 1.3em;
  font-weight: 700;
}

.modal__text {
  text-align: center;
}

.modal__answer {
  margin-left: 0.2em;
  color: var(--page-text-color);
}

:root {
  font-size: calc(0.5em + 1vw);
  line-height: 1.3;
  box-sizing: border-box;
  font-family: "Bitter", "Arial", serif;
  font-weight: normal;
  font-display: swap;
  height: 100%;
  background-color: var(--page-color);
  background-image: linear-gradient(90deg, var(--page-color), var(--page-color-gradient));
  color: var(--page-text-color);
  overflow-x: hidden;
}
@media (max-width: 600px) {
  :root {
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  :root {
    font-size: 20px;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  padding: 0;
  margin: 0;
}

button {
  border: none;
  cursor: pointer;
  background-color: transparent;
}

svg path {
  pointer-events: none;
}

*,
::before,
::after {
  box-sizing: inherit;
}

.double-container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 1px auto;
  user-select: none; /* Для современных браузеров */
  -webkit-user-select: none; /* Для Safari */
  -moz-user-select: none; /* Для Firefox */
  -ms-user-select: none; /* Для Internet Explorer/Edge */
}
@media (min-width: 768px) {
  .double-container {
    display: flex;
    height: 100vh;
    justify-content: center;
  }
}
@media (min-width: 1280px) {
  .double-container {
    align-items: center;
  }
}

